<template>
    <div id="home">
        <div class="page-menu">
            <ul class="drawer-menu">
                <li><div :class="status ? 'open-state' : ''"></div></li>
                <li><div :class="status ? 'open-state' : ''"></div></li>
                <li><div :class="status ? 'open-state' : ''"></div></li>
                <li><div :class="status ? 'open-state' : ''"></div></li>
                <li><div :class="status ? 'open-state' : ''"></div></li>
                <li><div :class="status ? 'open-state' : ''"></div></li>
                <li><div :class="status ? 'open-state' : ''"></div></li>
            </ul>
        </div>
        <div class="page-main" :class="status ? 'c-state' : ''" @click.stop="close()">
            <div class="mask" v-if="status"></div>
            <div class="header">
                <div class="logo">
                    <img :src="require('../../assets/img/logo.png')"/>
                </div>
                <div class="name"></div>
                <div class="menu" @click.stop="open()">
                    <img :src="require('../../assets/img/menu.png')"/>
                </div>
            </div>
            <div class="pc_header">
                <div class="pc_header_top_bg">
                    <ul class="pc_nav_top">
                        <li><div></div></li>
                        <li><div></div></li>
                        <li><div></div></li>
                        <li><div></div></li>
                        <div class="clear"></div>
                    </ul>
                </div>
                <div class="pc_header_bg">
                    <div class="logo">
                        <img :src="require('../../assets/img/logo.png')"/>
                    </div>
                    <div class="name"></div>
                    <div class="pc_nav_bg">
                        <ul class="pc_nav">
                            <li class="m-r-zero">
                                <div class="hwrapper">
                                    <div class="first"></div>
                                    <div class="second">DOWNLOAD</div>
                                </div>
                            </li>
                            <li>
                                <div class="hwrapper">
                                    <div class="first"></div>
                                    <div class="second">NEWS</div>
                                </div>
                            </li>
                            <li>
                                <div class="hwrapper">
                                    <div class="first"></div>
                                    <div class="second">JOIN US</div>
                                </div>
                            </li>
                            <li>
                                <div class="hwrapper">
                                    <div class="first"></div>
                                    <div class="second">CONTACT</div>
                                </div>
                            </li>
                            <li class="pmenu" @mouseenter="enter" @mouseleave="leave">
                                <div class="hwrapper">
                                    <div class="first"></div>
                                    <div class="second">PRODUCT</div>
                                </div>
                                <div class="plist" :class="pshow ? 'active' : ''"></div>
                            </li>
                            <li class="last">
                                <div class="hwrapper">
                                    <div class="first"></div>
                                    <div class="second">ABOUT US</div>
                                </div>
                            </li>
                            <div class="clear"></div>
                        </ul>
                    </div>
                    <div class="clear"></div>
                </div>
            </div>
            <div class="content">
                <div class="banner">
                    <Banner :list="list"></Banner>
                </div>
                <div class="bg_grey">
                    <div class="service">
                        <Service></Service>
                    </div>
                </div>
                <div class="bg_white">
                    <div class="case">
                        <Case></Case>
                    </div>
                </div>

                <div class="bg_grey">
                    <div class="news">
                        <News></News>
                    </div>
                </div>

                <div class="about" :class="aboutActive ? 'active' : ''">
                    <div class="content">
                        <div class="n-title">
                            <div class="p1">9</div>
                            <div class="p2"></div>
                        </div>
                        <div class="detail">
                            <ul>
                                <li>VOCVOCVOC smart lockVOC DoorVOC Magnetic lockVOC smart SafesVOC | GS5</li>
                                <li>VOCVOC151700VOC70</li>
                                <li>VOC</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div class="footer-outer">
                <Foot></Foot>
            </div>
        </div>
    </div>
</template>
<script>
    import Vue from 'vue'
    import Banner from './Banner.vue'
    import Case from './Case.vue'
    import News from './News.vue'
    import Service from './Service.vue'
    import Foot from './Foot.vue'

    export default {
        data(){
            return {
                list: [
                    {src: require('../../assets/img/banner1.jpg'), desc: '', name: 'item 1'},
                    {src: require('../../assets/img/banner2.jpg'), desc: '', name: 'item 2'},
                    {src: require('../../assets/img/banner3.jpg'), desc: '', name: 'item 3'},
                    {src: require('../../assets/img/banner4.jpg'), desc: '', name: 'item 4'}
                ],
                status: false,
                pshow: false,
                aboutActive: false
            }
        },
        methods: {
            open(){
                if (this.status) {
                    this.status = false;
                } else {
                    this.status = true;
                }
            },
            close(){
                if (this.status) {
                    this.status = false;
                }
            },
            enter(){
                this.pshow = true;
            },
            leave(){
                this.pshow = false;
            },
            scroll(){
                let obj = document.querySelector(".page-main");
                let swidth = document.body.clientWidth;
                let screenHeight = obj.offsetHeight;   //
                let scrollTop = obj.scrollTop;   //

                let contentObj = document.querySelector(".content");
                let aboutObj = document.querySelector(".about");
                let aboutObjH= aboutObj.offsetHeight;
                let aboutObjT= aboutObj.offsetTop + contentObj.offsetTop;

                if(swidth >= 678){
                    /**
                     * aboutObjT < (scrollTop + screenHeight)    
                     * (aboutObjT + aboutObjH) > scrollTop   
                     */
                    if((aboutObjT + aboutObjH) >= scrollTop && aboutObjT < (scrollTop + screenHeight)){
                        this.aboutActive = true;
                    }
                    else{
                        this.aboutActive = false;
                    }
                }
            }
        },
        components: {
            Banner,
            Case,
            News,
            Service,
            Foot
        },
        mounted() {
            var contentObj = document.querySelector(".page-main");
            contentObj.addEventListener('scroll', this.scroll);
        }
    }

</script>
<style>
    ul{
        list-style: none;
        padding: 0px;
        margin: 0px;
    }
    a {
        color: #363f44;
        text-decoration: none;
        cursor: pointer;
    }
    a:hover,
    a:focus {
        color: #141719;
        text-decoration: none;
    }

    @import '../../assets/css/home.css';
</style>


